export function getChadwicksIndexes() {
  return {
    description: 0,
    qty: 1,
    unitCost: 2,
    lineAmountExclVAT: 3,
    itemNo: 4,
  };
}

export function replaceKnownWrongCodeAndQuantity(row) {
  const _row = [...row];

  if (_row[getChadwicksIndexes().itemNo] === 'UZR-00000000-02') {
    _row[getChadwicksIndexes().itemNo] = 'UZR-0000000-50';
    _row[getChadwicksIndexes().qty] = _row[getChadwicksIndexes().qty] / 25;
  }

  return _row;
}
